import request from '../../utils/request.js'
module.exports = {
	data(){
		return {
			userInfo: {
				
			},
			personalList: [
				{
					name: '我的订单',
					icon: 'icon-dingdan11'
				},
				{
					name: '我的拼团',
					icon: 'icon-pintuandingdan'
				},
				{
					name: '领红包',
					icon: 'icon-tubiaolunkuo-1'
				},
				{
					name: '我的积分',
					icon: 'icon-jifen3'
				},
				{
					name: '地址管理',
					icon: 'icon-dizhiguanli'
				},
				{
					name: '账号安全',
					icon: 'icon-dingdan11'
				},
				{
					name: '联系客服',
					icon: 'icon-zhanghaoanquan'
				},
				{
					name: '用户反馈',
					icon: 'icon-tubiaolunkuo-'
				},
				{
					name: '帮助中心',
					icon: 'icon-bangzhuzhongxin'
				},
				{
					name: '退还/售后',
					icon: 'icon-shouhou'
				}
			]
		}
	},
	// onLoad(options){
	// 	console.log(options.userInfo)
	// },
	 mounted(){
		// 获取用户唯一标识
		wx.login({
			success:async (res) => {
				// 1. 获取临时登录凭证： code
				let code = res.code;
				// 2. 将code发请求交给服务器
				let result = await request('/getOpenId', {code})
				console.log(result)
			}
		})
		
		
		// this.$mp.query.userInfo： 等同于onLoad中的options，用于获取路由传参的数据
		if(this.$mp.query.userInfo){
			let userInfo = JSON.parse(this.$mp.query.userInfo)
			this.userInfo = userInfo
		}else {
			wx.getUserInfo({
				success:(res) => {
					this.userInfo = res.userInfo
				},
				fail: () => {
					console.log('获取失败')
				}
			})
		}
	},
	methods: {
		toLogin(){
			if(this.userInfo.nickName){
				return
			}
			wx.navigateTo({
				url: '/pages/login/login'
			})
		}
	}
	
}
